.header{
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: calc(10px + 2vmin);
  color: black;
  height: 15vh;
  z-index: 108; 
  width: 100%;
}

.header div.logo img{
  height: 60px;
  text-decoration: none;
  cursor: pointer;
  margin-left: 80px;
}
.header-button{
  font-size: 25px;
  padding: 12px 1px;
  background: none;
  color: #000;
  cursor: pointer;
  margin-right: 5rem;
  margin-left: -2rem;
  margin-top: 10px;
  justify-content: left;
  border: none;
  outline:none;
}
.header-icon {
  font-size: 25px;
  padding: 12px 0px;
  background: none;
  color: #000000;
  cursor: pointer;
  margin-left: -0.5rem;
  margin-top: 1px;
  border: none;
  outline:none;
}
.header-searchInput{
  width: 500px;
  height: 40px;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  color: #4e4b4b;
  border: 2px solid #F15A22;
  outline:none;
  visibility: visible;
  text-align: start;
  padding-right: 6px;
  margin-left: -125px;
}
/* input[type=text] {
  font-size: 15px;
} */
.header-searchIcon{
  height: 37px !important;
  width: 50px !important;
  background-color: #F15A22;
  border-bottom-right-radius: 50px;
  border-top-right-radius: 50px;
  margin-left: -2px;
  margin-top: 10px;
  margin-bottom: -19px;
  margin-right: 0px;
  padding-top: 8px;
  padding-bottom: 1px;
  font-weight: lighter !important;
  cursor: pointer;
  font-size: 5rem !important;
    
}
::-webkit-input-placeholder { 
  color: grey;
  padding-left: 20px;
}

:-ms-input-placeholder { 
  color: grey;
  padding-left: 20px;
}
.cart-logo img{
    height: 60px;
    margin-right: 155px;
    cursor: pointer;
}
.amount{
vertical-align: top;
position: absolute;
top: 11%;
font-size: 15px;
margin-left: -158px;
cursor: pointer;
font-weight: 700;
word-spacing: 0px;
letter-spacing: -0.14px;
line-height: 40px;
display: inline;
}
.count-product{
vertical-align: top;
position: absolute;
top: 15%;
font-size: 11px;
margin-left: -160px;
cursor: pointer;
font-weight: 600;
background-color:#F15A22;
width: 25px;
text-align: center;
border-radius:50%;
  -moz-border-radius:50%;
  -webkit-border-radius:80%;
  color: #ffffff;
  height: 20.8px;
padding-top: 2px;
}

/* Float the link section to the right */
.header-right {
  float: right;
}

.content{
  text-align: center;
  width: 100%;
}

.cart-dropdown {
  position: absolute;
  width: 240px;
  height: 340px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-left: 1px solid rgb(177, 172, 172);
  border-bottom: 1px solid rgb(177, 172, 172);
  border-right: 1px solid rgb(177, 172, 172);
  border-top: 1px solid #F15A22;
  background-color: white;
  top: 155px;
  right: 40px;
  z-index: 105;
  .cart-items {
    height: 280px;
    display: flex;
    flex-direction: column;
    overflow: auto;
  }
}


.empty-message{
  margin: 50px auto;
}


.cart-dropdown-btn {
min-width: 165px;
width: auto;
height: 50px;
letter-spacing: 0.5px;
line-height: 50px;
padding: 0 35px 0 35px;
font-size: 15px;
background-color: #F15A22;
color: white;
text-transform: uppercase;
font-family: 'Open Sans Condensed';
font-weight: bolder;
border: none;
cursor: pointer;
display: flex;
justify-content: center;
}
.cart-item {
width: 100%;
display: flex;
height: 80px;
margin-bottom: 15px;
}
.imgproduct {
  width: 30%;
}
.item-details {
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 10px 20px;
}
  .cart-name {
    font-size: 16px;
  }
  .cart-price {
    font-size: 15px;
  }

.rm-cart-btn {
  padding: 12px;
  cursor: pointer;
  font-size: 15px;
  color: red;
} 
.sticky + .cart-dropdown{

background-color: white;
position: fixed;
width: 240px;
height: 340px;
display: flex;
flex-direction: column;
padding: 20px;
border-left: 1px solid rgb(177, 172, 172);
border-bottom: 1px solid rgb(177, 172, 172);
border-right: 1px solid rgb(177, 172, 172);
border-top: 3px solid #F15A22;
top: 89px;
right: 40px;
z-index: 105;

}




.sticky {
position: fixed;
top: 0;
width: 100%;
height: 12vh;
box-shadow: 0px 4px 8px 0px rgba(0,0,0,0.4);
}
.sticky + .content {
  padding-top: 60px;
}
.sticky .amount{
vertical-align: top;
top: 28%;
font-size: 15px;
margin-left: -160px;
cursor: pointer;
font-weight: 700;
word-spacing: 0px;
letter-spacing: -0.14px;
line-height: 40px;
display: inline;
}
.sticky  .count-product{
vertical-align: top;
position: absolute;
top: 60%;
font-size: 11px;
margin-left: -158px;
cursor: pointer;
font-weight: 600;
background-color:#F15A22;
width: 25px;
text-align: center;
border-radius:50%;
  -moz-border-radius:50%;
  -webkit-border-radius:80%;
  color: #ffffff;
  height: 20.8px;
padding-top: 2px;
}


@media screen and (max-width: 500px) {
  .header div:not(:first-child) {display: none;}
  .header .icon {
    float: left;
    display: block;
    visibility: visible;
  }
  .header{box-shadow: none;}
}


@media screen and (max-width: 500px) {

  .header a {
    float: none;
    display: block;
    text-align: left;
  }
  .header-right {
    float: none;
  }
  .header{
    box-shadow: none;
    border-bottom: 0.1em #333 solid;
    height: 50px;
  }
  .header div.logo img{height: 30px;}
  .header-button{
    float: left; 
    position: fixed; 
    left: 1; top: 0; 
    margin-left: -16rem;
    margin-top: 0;
  }
  
}