.slide-container {
    max-width: 1600px;
    margin-top: 1px;
    box-shadow: 0 10px 8px 0 rgba(54, 54, 54, 0.2), 0 1px 1px 0 rgba(255, 255, 255, 0.19);
    z-index: 1 ;
}
 

.each-fadeone-a {
    display: flex;
}
  
.each-fadeone {
    width: 100%;
    position: relative;
    display: flex;
    background-image: linear-gradient(to bottom, #ffffff, #dfdf8e);
    height: 300px;
}
.each-fadeone-a {
    width: 100%;
    position: relative;
    display: flex;
    background-image: linear-gradient(to bottom, #ffffff, #dfdf8e);
    height: 300px;
}
.each-fadeone img{
    width: 30%;

}
.each-fadeone-a img{
    width: 65%;
}
 
.each-fadeone h2 {
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    color: #e94d31;
    font-size: 3.5em;
}
.each-fadeone-a h2 {
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    color: #e94d31;
    font-size: 3rem;
}

.center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 20px;
}
.topleft{
    position: absolute;
    margin-top: 1em;
    margin-left: 3em;
    left: 16px;
}
.topright {
    position: absolute;
    margin-top: 1em;
    margin-right: 4em;
    right: 16px;
}
.promo-banner{
    display: flex;
    justify-content: space-evenly;
}

  @media screen and (max-width: 500px) {
    .slide-container{ margin: 0; padding: 0;}
    .each-fadeone{height: 100px;}
    .each-fadeone .topleft{
        height: 120px; 
        padding: 0;
        margin: 0;}
    .each-fadeone .topright{
        height: 100px; 
        padding: 0;
        margin: 0;}
    .each-fadeone-a{height: 100px;}
    .each-fadeone h2{font-size: 1rem;}
    .each-fadeone-a img{height:120px ;}
    .each-fadeone-a .container img{
        height: 45px;
        width: 150px;
        margin-top: 15px;
    }.each-fadeone-a img{height: 120px; 
        padding: 0;
        margin: 0;}
    .each-fadeone-a .container .btn{
        top: 17%; 
        position: fixed;
        float: left;
        left: 0;
        margin-left: 70px;
        margin-top: 10px;
        width: 70px;
        font-size: 10px;
        padding: 0;
        font-weight: 20px;
        height: 25px;
    }
  }

.container {
    position: relative;
    width: 30%;
    height: 100px;
}

  /* Make the image responsive */
.container img {
    width: 100%;
    height: 80px;
    margin-top: 5em;
}
  
  /* Style the button and place it in the middle of the container-one/image */
.container .btn {
    position: absolute;
    margin-top: 13rem;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    background-color: rgb(31, 28, 28);
    color: white;
    font-size: 16px;
    padding: 12px 24px;
    border: none;
    cursor: pointer;
    border-radius: 50px;
    width: 10em;
}
  
.container .btn:hover {
    background-color: black;
}

/* first slide end*/






