.footer{
    display: block;
    margin-top:120px;
}
.newsletter{
    display: flex;
    background: #F15A22;
    border-top: 15px black solid;
    color: white;
    margin: auto;
    justify-content: space-evenly;
    align-items: center;
    text-align: justify;
}
.newsletter p{
    margin-left: -50px;
    height: 45px;
    font-size-adjust: 20px;
    margin-top: 1.8rem;
    font-size: 15px;
}
.news-email {
    padding-right: 1rem;
    height: 36px;
    width: 330px;
    font-size-adjust: 20px;
    margin-top: 1.8rem;
    margin-bottom: 1.8rem;
    outline: none;
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
    border: none;
}
.submit{
    margin-left: -101px;
    height: 37.6px;
    font-size-adjust: 20px;
    margin-top: 1.8rem;
    margin-bottom: 1.8rem;
    outline: none;
    cursor: pointer;
    border: none;
    width: 100px;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    color: white;
    background-color: rgb(51, 50, 50);
}
.newsletter h2{
    margin-left: -95px;
    height: 45px;
    font-size: 25px;
}
.footer-menu{
    display: flex;
    background: rgb(255, 255, 255);
    height: 80%;
    margin-bottom: 0px;
    justify-content: space-evenly;
    margin-top: 40px;
    margin-bottom: 40px;
}
.copyright {
    background: whitesmoke;
    width: 100%;
    height: 50px;
    margin-bottom: 0px;
    margin-top: 5px;
    padding-top: 1rem;
    margin: auto;
    text-align: center;
    color: black;
}
.footer-menu-link{
    display: block;
    cursor: pointer;
    font-size-adjust: 10px;
    line-height: 1rem;
}
.footer-menu-link img{
    height: 60px;
    text-decoration: none;
}

.sendicon{
    height: 45px !important;
    width: 30px !important;
    margin-right: 0%;
    padding-right: 0%;
}
::-webkit-input-placeholder { 
    color: grey;
    padding-left: 20px;
  }
  
  :-ms-input-placeholder { 
    color: grey;
    padding-left: 20px;
  }