.row {
    display: flex;
    padding: 0 4px;
    justify-content: center;
    height: 100%;
    margin-top: 28px;
    margin-left: 100px;
    margin-right: 100px;
  }
  
  .column img {
    width: 100%;
    border: .5rem white solid;
    height: 80%;
  }
  
  /* Responsive layout - makes a two column-layout instead of four columns */
  @media screen and (max-width: 500px) {
    .row {
      display: block;
      width: 80%;
      margin: auto;
      margin-top: 15px;
    }
  }
  
